section {
    width : 100vw;
    height : 100vh;
    border-bottom : solid grey 2px;
}
.s1 {
    background-image: url('./1.png');
    background-size: cover;
}
.s2 {
    background-image: url('./3.png');
    background-size: cover;
}
.s3 {
    background-image: url('./2.png');
    background-size: cover;
}
.s4 {
    background-image: url('./4.png');
    background-size: cover;
}
.p1 {
    display: flex;
    justify-content: center;
    top : 5%;
    max-width: 25vw;
    left: 25%;
    font-size: larger;
    font-weight: 500;
}
.p2 {
    display: flex;
    justify-content: center;
    top : 5%;
    max-width: 25vw;
    left: 25%;
    font-size: small;
    font-weight: 700;
}
.p3 {
    display: flex;
    justify-content: center;
    top : 5%;
    max-width: 25vw;
    left: 25%;
    font-size: medium;
    font-weight: 500;
}
.p4 {
    display: flex;
    justify-content: center;
    top : 5%;
    max-width: 26vw;
    left: 25%;
    font-size: large;
    font-weight: 500;
}

@media (orientation : portrait){
    .dg {
        flex-direction: column;
    }
    img {
        display: none;
    }
    .p1,.p2,.p3,.p4 {
        max-width : 80vw;
        font-weight: 500;
    }
    .s4 {
        height : 110vh;
    }
    .p3,.p1 {
        margin-left: 10vw;
        max-width: 95vw;
    }
    .p2,.p4 {
        margin-left : 10vw;
        text-align: center;
    }
}


body {
    background-color: gainsboro;
}

.dg {
    display: flex;
    margin-top: 5%;
    justify-content: center;
}
.d1 {
    margin-right : 2%;
    border-right : 1px solid gray;
    padding : 10px;
}
.d2 {
    margin-left : 2%;
}
img {
    width : 34vw;
    height : 26vw;
}
.H {
    font-family: 'Montserrat', sans-serif;
    display: flex;
    justify-content: center;
    margin: 0;
    padding: 0;
    position: relative;
    text-align: center;
    animation: appear 2s linear;
    top:5%;
    font-size: xx-large;
    color : rgb(255, 0, 0);
    transform: scale(1.2);
    animation: appear 1s;
}
@keyframes appar {
    from {
        top : 10%;
    }
    to {
        top : 5%;
        color: blue;
    }
}
