@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.h1 {
    font-family: 'Montserrat', sans-serif;
    display: flex;
    justify-content: center;
    margin: 0;
    padding: 0;
    position: relative;
    text-align: center;
    -webkit-animation: appear 2s linear;
            animation: appear 2s linear;
    top:35%;
    color : rgb(255, 0, 0);
    transform: scale(1.2);
}
.h2 {
    font-family: 'Montserrat', sans-serif;
    margin: 0;
    padding: 0;
    position: absolute;
    text-align: center;
    -webkit-animation: appear 2s linear;
            animation: appear 2s linear;
    color : rgb(255, 0, 0);
    transform: scale(1.2);
    margin-left:75vw;
    display: flex;
    margin-bottom: 50vh;
}
.overflow {
    overflow-y: visible;
}
.h1::before {
    content: '';
    width : 60%;
    height:5px;
    background: rgb(255, 0, 0);
    display: block;
    left: 20%;
    top: 100%;
    position: absolute;
    -webkit-animation: appearBefore 4s linear;
            animation: appearBefore 4s linear;
    z-index: 100;
}


.HeroImage {
    width : 100vw;
    height : 100vh;
    background-image: url(/HistoriaTerroryzm/static/media/wtcg.fd904d1a.jpg);
    background-size: cover;
    transition: 1.5s linear;
}

.btn {
    display: flex;
    justify-content: center;
    margin-top : 50vh;
}
.hbtn {
    background-color: transparent;
    border: red solid 0.25vh;
    color:red;
    font-size: xx-large;
    transition: 1s;
    font-family: 'Montserrat', sans-serif;
    -webkit-animation: appear 4s linear;
            animation: appear 4s linear;
}
.hbtn:hover {
    background-color: red;
    color : white;
}
.zawinieta {
    height : 45vh;   
}
.zawinietybtn {
    opacity: 0;
    transform: scale(0);
}
body {
    overflow: hidden;
}

@-webkit-keyframes appear {
    from {
        opacity: 0;
        transform: translateY(25vh);
    }
    to {
        opacity: 1;
    }
}

@keyframes appear {
    from {
        opacity: 0;
        transform: translateY(25vh);
    }
    to {
        opacity: 1;
    }
}
@-webkit-keyframes appearBefore {
    from {
        transform: scaleX(0);
    }
    to {
        transform: scaleX(1);
    }
}
@keyframes appearBefore {
    from {
        transform: scaleX(0);
    }
    to {
        transform: scaleX(1);
    }
}

@media screen and (max-width:1024px){
    h1::before{
        width:60%;
        left:20%;
    }
}
section {
    width : 100vw;
    height : 100vh;
    border-bottom : solid grey 2px;
}
.s1 {
    background-image: url(/HistoriaTerroryzm/static/media/1.ea5213b2.png);
    background-size: cover;
}
.s2 {
    background-image: url(/HistoriaTerroryzm/static/media/3.e78508fd.png);
    background-size: cover;
}
.s3 {
    background-image: url(/HistoriaTerroryzm/static/media/2.cd919bae.png);
    background-size: cover;
}
.s4 {
    background-image: url(/HistoriaTerroryzm/static/media/4.802a1228.png);
    background-size: cover;
}
.p1 {
    display: flex;
    justify-content: center;
    top : 5%;
    max-width: 25vw;
    left: 25%;
    font-size: larger;
    font-weight: 500;
}
.p2 {
    display: flex;
    justify-content: center;
    top : 5%;
    max-width: 25vw;
    left: 25%;
    font-size: small;
    font-weight: 700;
}
.p3 {
    display: flex;
    justify-content: center;
    top : 5%;
    max-width: 25vw;
    left: 25%;
    font-size: medium;
    font-weight: 500;
}
.p4 {
    display: flex;
    justify-content: center;
    top : 5%;
    max-width: 26vw;
    left: 25%;
    font-size: large;
    font-weight: 500;
}

@media (orientation : portrait){
    .dg {
        flex-direction: column;
    }
    img {
        display: none;
    }
    .p1,.p2,.p3,.p4 {
        max-width : 80vw;
        font-weight: 500;
    }
    .s4 {
        height : 110vh;
    }
    .p3,.p1 {
        margin-left: 10vw;
        max-width: 95vw;
    }
    .p2,.p4 {
        margin-left : 10vw;
        text-align: center;
    }
}


body {
    background-color: gainsboro;
}

.dg {
    display: flex;
    margin-top: 5%;
    justify-content: center;
}
.d1 {
    margin-right : 2%;
    border-right : 1px solid gray;
    padding : 10px;
}
.d2 {
    margin-left : 2%;
}
img {
    width : 34vw;
    height : 26vw;
}
.H {
    font-family: 'Montserrat', sans-serif;
    display: flex;
    justify-content: center;
    margin: 0;
    padding: 0;
    position: relative;
    text-align: center;
    -webkit-animation: appear 2s linear;
            animation: appear 2s linear;
    top:5%;
    font-size: xx-large;
    color : rgb(255, 0, 0);
    transform: scale(1.2);
    -webkit-animation: appear 1s;
            animation: appear 1s;
}
@-webkit-keyframes appar {
    from {
        top : 10%;
    }
    to {
        top : 5%;
        color: blue;
    }
}
@keyframes appar {
    from {
        top : 10%;
    }
    to {
        top : 5%;
        color: blue;
    }
}

