@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@600&display=swap');

.h1 {
    font-family: 'Montserrat', sans-serif;
    display: flex;
    justify-content: center;
    margin: 0;
    padding: 0;
    position: relative;
    text-align: center;
    animation: appear 2s linear;
    top:35%;
    color : rgb(255, 0, 0);
    transform: scale(1.2);
}
.h2 {
    font-family: 'Montserrat', sans-serif;
    margin: 0;
    padding: 0;
    position: absolute;
    text-align: center;
    animation: appear 2s linear;
    color : rgb(255, 0, 0);
    transform: scale(1.2);
    margin-left:75vw;
    display: flex;
    margin-bottom: 50vh;
}
.overflow {
    overflow-y: visible;
}
.h1::before {
    content: '';
    width : 60%;
    height:5px;
    background: rgb(255, 0, 0);
    display: block;
    left: 20%;
    top: 100%;
    position: absolute;
    animation: appearBefore 4s linear;
    z-index: 100;
}


.HeroImage {
    width : 100vw;
    height : 100vh;
    background-image: url('./wtcg.jpg');
    background-size: cover;
    transition: 1.5s linear;
}

.btn {
    display: flex;
    justify-content: center;
    margin-top : 50vh;
}
.hbtn {
    background-color: transparent;
    border: red solid 0.25vh;
    color:red;
    font-size: xx-large;
    transition: 1s;
    font-family: 'Montserrat', sans-serif;
    animation: appear 4s linear;
}
.hbtn:hover {
    background-color: red;
    color : white;
}
.zawinieta {
    height : 45vh;   
}
.zawinietybtn {
    opacity: 0;
    transform: scale(0);
}
body {
    overflow: hidden;
}

@keyframes appear {
    from {
        opacity: 0;
        transform: translateY(25vh);
    }
    to {
        opacity: 1;
    }
}
@keyframes appearBefore {
    from {
        transform: scaleX(0);
    }
    to {
        transform: scaleX(1);
    }
}

@media screen and (max-width:1024px){
    h1::before{
        width:60%;
        left:20%;
    }
}